* {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  color: white;
  background-color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
  font-weight: 400;
}

.about-aboutPhoto {
  margin-left: 7%;
  margin-right: 0;
  width: 90%;
}

.about-container {
  background-image: url('./images/digoHeroLandAlt.jpeg');
  background-repeat: no-repeat;
  background-size: 100%;
  height: 50em;
  margin: 0 auto;
  padding-top: 6em;
  width: 100%;
}

.about-content-container {
  display: flex;
  margin: 0 auto;
  width: 80%;
}

.about-infos-container {
  background-color: #1e2735ee;
  border-radius: 1em;
  margin-left: 30%;
  padding: 1.5em;
  width: 70%;
}

.about-photo-container {
  width: 30%;
}

.about-h2 {
  color: white;
  font-family: 'Black Ops One', cursive;
  font-size: 2em;
  font-weight: 400;
  margin: 1em auto 1.5em;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.2);
  width: 80%;
}

.about-h3 {
  color: white;
  font-family: 'Black Ops One', cursive;
  font-size: 1.5em;
  font-weight: 400;
  margin: 1.3em 0 0.5em;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.2);
}

.about-knowledge-container {
  margin: 0 auto;
  width: 80%;
}

.about-knowledge-lists {
  display: flex;
  justify-content: space-between;
}

.about-knowledge-list-item {
  font-size: 1em;
  font-weight: 700;
  letter-spacing: 0.06em;
  line-height: 1.6em;
  word-spacing: 0.2em;
}

.about-text-container {
  margin: 0 auto;
  width: 100%;
}

.about-p {
  font-size: 1em;
  font-weight: 700;
  letter-spacing: 0.06em;
  line-height: 1.8em;
  text-align: center;
  word-spacing: 0.2em;
}

.buttonTop-arrowUp {
  border: white 2px solid ;
  border-radius: 100%;
  box-shadow: 0 0 5px #1e2735;
  /* animation: pulse 1.5s infinite; */
}



.buttonTop-container {
  animation: pulse 1.2s infinite;
  border-radius: 100%;
  position: sticky;
  bottom: 7em;
  height: 0em;
  left: 78em;
  width: 3em;
  z-index: 100;
}

@keyframes pulse {
  0% {
    transform: (scale(0.9));
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 15px #1e2735;
  }
  100% {
    transform: (scale(0.7));
    box-shadow: 0 0 5px #1e2735;
  }
}

.contact-container {
  background-color: #1e2735;
  height: 30em;
  margin: 0 auto;
  padding-top: 6em;
  width: 100%;
}

.contact-h2 {
  color: white;
  font-family: 'Black Ops One', cursive;
  font-size: 2em;
  font-weight: 400;
  margin: 0 auto 3em;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.2);
  width: 80%;
}

.contact-link {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  font-weight: 700;
  letter-spacing: 0.06em;
  line-height: 1.8em;
  text-align: center;
  word-spacing: 0.2em;
}

.contact-link-image {
  margin-bottom: 1em;
  width: 30%;
}

.contact-link :hover{
  transition-timing-function: ease-in-out;
  transition: width 0.8s;
  width: 45%;
}

.contact-link-item {
  width: 30%;
}

.contact-link-list {
  display: flex;
  justify-content: space-around;
}

.contact-links-container {
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  width: 80%;
}

.footer-container {
  background-color: #2f2f2f;
  display: flex;
  justify-content: center;
  padding: 0.3em 5%;
}

.footer-p {
  color: rgba(255, 255, 255, 0.1);
  font-family: 'Black Ops One', cursive;
}

.header-brand {
  font-family: 'Black Ops One', cursive;
}

.header-container {
  background-color: #2f2f2f;
  display: flex;
  justify-content: space-between;
  padding: 0.3em 5%;
}

.header-menu {
  display: flex;
  justify-content: space-between;
}

.header-menu-container {
  width: 30%;
}

.header-menu-link {
  color: white;
  font-family: 'Black Ops One', cursive;
  font-size: 0.9em;
  font-weight: 400;
}

.header-menu-item :hover {
  color: white;
  transition: 0.4s;
}

